import * as React from "react";
import './childlist.css';

const image_data = "";

const ChildList = () => {

  const [actor, setActor] = React.useState(null);

  React.useEffect(() => {
    import("../actor").then((module) => {
      setActor(module.default);
      getChildren();
    });
  }, []);

  function getChildren() {
    actor?.getChildren().then((returnedChilren) => {
      if (!returnedChilren.length) {
        return alert("No children - please add a child to begin");
      }
      console.log(returnedChilren);
    });
    return false;
  }

  function getChild(e) {
    e.preventDefault();
    const child = e.target.querySelector('input[name="child_id"]').value;

    actor?.getChild(child).then((returnedChild) => {
      if (!returnedChild.length) {
        return alert("No children with that ID");
      }

      console.log(returnedChild);
    });
    return false;
  }

  // const ProfilePic = ({ data }) => <img src={`data:image/jpeg;base64,${data}`} />

  return (
      <section>
        <h2>My Children</h2>
        <div className="section">

{/* TODO - pull data from returnedChilren into the grid below */}

        <div className="col grid_1_of_4">

        {/* <ProfilePic data={image_data} /> */}

{/* BUG with atob. deployment fails, hack = comment out img tag, dfx deploy, then uncomment */}
          {/* <img
              style={{ maxWidth: "75px" }}
              src={atob(image_data)}
              alt="profile"
            />  */}
          </div>
          <div className="col grid_1_of_4"> Elodie </div>
          <div className="col grid_1_of_4"> DooCoins balance: 0 </div>
          <div className="col grid_1_of_4"> Edit / Delete </div>
        </div>

        <form onSubmit={getChild}>
          <label htmlFor="child_id">
            <input type="text" name="child_id" id="child_id" />
          </label>
          <button type="submit">Search children</button>
        </form>
      </section>
  );
};

export default ChildList;